import React from 'react';
import Main from '../../components/Main/Main';
import { useParams } from 'react-router-dom';
import PropTypes from 'prop-types';

/**
 * Page de profil utilisateur
 *
 * @param {boolean} mockedData - Indique si les donnes doivent tre simules
 * @returns {React.ReactComponentElement} - lment React reprsentant la page de profil
 */
function ProfilPage({ mockedData }) {
  // Utilise useParams pour extraire l'identifiant d'utilisateur de l'URL
  let { userId } = useParams();

  return (
    <div className="profilPage">
      {/* Rend le composant Main avec les donnes extraites de l'URL et la proprit mockedData */}
      <Main userId={parseInt(userId)} mockedData={mockedData} />
    </div>
  );
}

// Spcifie les types des proprits attendues avec PropTypes
ProfilPage.propTypes = {
  mockedData: PropTypes.bool,
};

// Exporte le composant ProfilPage en tant que composant par dfaut
export default ProfilPage;
