// Format des donnes de performance
/**
 * Formate les donnes de performance en ajoutant des types de performance en franais.
 *
 * @param   {object}  data  Donnes de performance
 *
 * @return  {array}         Donnes de performance formates
 */
function formatPerformanceData(data) {
  // Traduction franaise des types de performance
  const frenchTranslation = [
    'Cardio',
    'Energie',
    'Endurance',
    'Force',
    'Vitesse',
    'Intensit',
  ];

  // Ajoute les types de performance  la proprit kind de l'objet data
  for (let i = 0; i < frenchTranslation.length; i++) {
    data.kind[i + 1] = frenchTranslation[i];
  }

  // Associe chaque lment de data.data  un type de performance
  data.data.forEach((el, index) => (el.kind = data.kind[index + 1]));

  // Inverse l'ordre des donnes
  data.data.reverse();

  // Retourne les donnes de performance formates
  return data.data;
}

// Format des donnes moyennes
/**
 * Ajoute des sessions fictives au dbut et  la fin du tableau sessions.
 *
 * @param   {object}  data  Donnes moyennes
 *
 * @return  {array}         Sessions moyennes mises  jour
 */
function addDaysAverageSessions(data) {
  const { sessions } = data;

  // Supprime le premier et le dernier lment s'il n'y a pas exactement 7 lments
  if (data.sessions.length !== 7) {
    data.sessions.shift();
    data.sessions.pop();
  }

  // Ajoute des sessions fictives au dbut et  la fin du tableau sessions
  sessions.unshift({
    day: '',
    sessionLength: 15,
  });
  sessions.push({
    day: '',
    sessionLength: 15,
  });

  // Retourne les sessions moyennes mises  jour
  return sessions;
}

// Format des jours de la semaine pour les sessions moyennes
/**
 * Ajoute des jours de la semaine (lettre)  chaque session moyenne.
 *
 * @param   {array}  sessions  Sessions moyennes
 *
 * @return  {array}            Sessions moyennes formates
 */
function formatAverageSessionDate(sessions) {
  // Jours de la semaine (lettre)
  const weekDay = ['', 'L', 'M', 'M', 'J', 'V', 'S', 'D', ''];

  // Associe chaque session  un jour de la semaine
  for (let i = 0; i < weekDay.length; i++) {
    sessions[i].day = weekDay[i];
  }

  // Retourne les sessions moyennes formates
  return sessions;
}

// Format des dates des sessions d'activit
/**
 * Reformate la date des sessions d'activit en utilisant uniquement le numro du jour.
 *
 * @param   {object}  data  Donnes d'activit
 *
 * @return  {array}         Sessions d'activit formates
 */
function formatActivityDate(data) {
  const { sessions } = data;

  // Reformate la date des sessions d'activit
  sessions.forEach((session) => {
    let result = session.day.substring(
      session.day.length - 2,
      session.day.length
    );
    session.day = result[0] === '0' ? result[1] : result;
  });
  // Retourne les sessions d'activit formates
  return sessions;
}

// Format des donnes utilisateur
/**
 * Renomme la proprit todayScore en score (si elle existe).
 *
 * @param   {object}  data  Donnes utilisateur
 *
 * @return  {object}        Donnes utilisateur formates
 */
function formatTodayScoreAttribute(data) {
  // Si la proprit todayScore existe, la renomme en score et supprime todayScore
  if (data.todayScore) {
    data.score = data.todayScore;
    delete data.todayScore;
  }

  // Retourne les donnes utilisateur formates
  return data;
}

/**
 * Met  l'chelle le score en pourcentage si celui-ci est infrieur  1.
 *
 * @param   {object}  data  Donnes utilisateur
 *
 * @return  {object}        Donnes utilisateur formates
 */
function formatScore(data) {
  data.score < 1 && (data.score = data.score * 100);

  // Retourne les donnes utilisateur formates
  return data;
}

// Exporte toutes les fonctions pour les rendre disponibles pour d'autres modules
export {
  formatPerformanceData,
  addDaysAverageSessions,
  formatAverageSessionDate,
  formatActivityDate,
  formatTodayScoreAttribute,
  formatScore,
};
