import React, { useState, useEffect } from 'react';
import propTypes from 'prop-types';
import Error from '../Error/Error';
import MainHeader from '../MainHeader/MainHeader';
import MainSection from '../MainSection/MainSection';
import {
  getUserData,
  getActivity,
  getAverageSession,
  getUserPerformance,
} from '../../services/dataManager';

/**
 *
 * @param {number} userId
 * @param {string} mockedData
 * @returns {React.ReactComponentElement}
 */
function Main({ userId, mockedData }) {
  // tats pour grer les donnes et le chargement
  const [isUserDataLoading, setIsUserDataLoading] = useState(false);
  const [isActivityDataLoading, setIsActivityDataLoading] = useState(false);
  const [isAverageSessionDataLoading, setIsAverageSessionDataLoading] =
    useState(false);
  const [isUserPerformanceDataLoading, setIsUserPerformanceDataLoading] =
    useState(false);
  const [isError, setIsError] = useState(false);

  // tats pour stocker les donnes utilisateur
  const [userData, setUserData] = useState({
    id: 0,
    keyData: {},
    score: 0,
    userInfos: {
      firstName: '',
      lastName: '',
      age: 0,
    },
    fill: '',
  });

  const [activityData, setActivityData] = useState([]); // Donnes activit
  const [averageSessionData, setAverageSessionData] = useState([]); // Donnes sessions
  const [userPerformanceData, setUserPerformanceData] = useState([]); // Donnes de performance

  // Effet secondaire pour charger les donnes utilisateur lors du montage du composant
  useEffect(() => {
    // Activation des indicateurs de chargement
    setIsUserDataLoading(true);
    setIsActivityDataLoading(true);
    setIsAverageSessionDataLoading(true);
    setIsUserPerformanceDataLoading(true);

    // Chargement des donnes utilisateur
    getUserData(userId, mockedData)
      .then((data) => setUserData(data))
      .then((_userData) => {
        // Dsactivation de l'indicateur de chargement et aucune erreur
        setIsUserDataLoading(false);
        setIsError(false);
      })
      .catch((_error) => setIsError(true));

    // Chargement des donnes d'activit
    getActivity(userId, mockedData)
      .then((data) => setActivityData(data))
      .then((_activityData) => setIsActivityDataLoading(false));

    // Chargement des donnes de sessions moyennes
    getAverageSession(userId, mockedData)
      .then((data) => setAverageSessionData(data))
      .then((_averageSessionData) => setIsAverageSessionDataLoading(false));

    // Chargement des donnes de performance
    getUserPerformance(userId, mockedData)
      .then((data) => setUserPerformanceData(data))
      .then((_userPerformanceData) => setIsUserPerformanceDataLoading(false));
  }, [mockedData, userId]);

  // Attribut de remplissage de la couleur pour les donnes utilisateur
  userData.fill = 'red';

  // Fonction pour grer l'affichage des donnes simules ou relles
  function mockedDataManager() {
    if (mockedData) {
      return <p className="green">Data Mocked</p>;
    }
    return <p className="red">Data Not Mocked</p>;
  }

  // Rendu conditionnel en cas d'erreur de chargement des donnes
  return isError ? (
    <main className="mainContainer">
      {mockedDataManager()}
      <Error />
    </main>
  ) : (
    // Rendu de la section principale avec les donnes utilisateur
    <main className="mainContainer">
      {mockedDataManager()}
      <MainHeader
        firstName={userData.userInfos.firstName}
        isUserDataLoading={isUserDataLoading}
      />
      <MainSection
        userData={userData}
        isUserDataLoading={isUserDataLoading}
        activityData={activityData}
        isActivityDataLoading={isActivityDataLoading}
        averageSessionData={averageSessionData}
        isAverageSessionDataLoading={isAverageSessionDataLoading}
        userPerformanceData={userPerformanceData}
        isUserPerformanceDataLoading={isUserPerformanceDataLoading}
      />
    </main>
  );
}

// Spcification des types de proprits attendues
Main.propTypes = {
  userId: propTypes.number,
  mockedData: propTypes.bool,
};

// Exportation du composant principal
export default Main;
